import {
  FETCH_ARTICLE,
  FETCH_ARTICLES,
  FETCH_TOP_STORIES,
  SORT_ARTICLES,
} from "../actions/articles";

const initialState = {
  article: null,
  articles: [],
  pages: null,
  pageSize: null,
  loading: true,
  section: "",
  topStories: [],
  sortValue: "newest",
};

function articles(state = initialState, action) {
  switch (action.type) {
    case FETCH_ARTICLES:
      const { section, results, pages, pageSize } = action.payload.response;
      return {
        ...state,
        articles: results,
        loading: false,
        pages,
        pageSize,
        section: section.webTitle,
      };

    case FETCH_ARTICLE:
      return {
        ...state,
        article: action.payload,
        loading: false,
      };

    case FETCH_TOP_STORIES:
      return {
        ...state,
        loading: false,
        pages: action.payload.response.pages,
        pageSize: action.payload.response.pageSize,
        section: action.payload.response.section.webTitle,
        topStories: action.payload.response.results,
      };

    case SORT_ARTICLES:
      return { ...state, sortValue: action.payload };

    default:
      return state;
  }
}

export default articles;
